import moment from 'moment';

const isOpen = (radnoVreme) => {
    // Kafi캖i ne smeju da se zatvaraju od lowerLimita 코to je 05:00 do po캜etka radnog vremena u toku dana
    // Da bi ovaj algoritam funkcionisao

    let time = radnoVreme,
        timeStart = time.split(' - ')[0],
        timeEnd = time.split(' - ')[1],
        currentTime = moment(),
        flag = false,
        sFlag = false,
        lowerLimit = '05:00'; //@todo Ovo treba da se istra쬴 koliko da bude na kraju

    if (currentTime.isBetween(moment('00:00', 'HH:mm'), moment(lowerLimit, 'HH:mm'))) {
        sFlag = true; //Ovo je flag koji ka쬰 da li je trenutno vreme pre코lo 00:00
    };

    if (moment(timeEnd, 'HH:mm').isBetween(moment('00:00', 'HH:mm'), moment(lowerLimit, 'HH:mm'))) {
        flag = true; //Ovo je flag koji ka쬰 da li je vreme zatvaranja kafi캖a izme캠u 00:00 i lowerlimita
    };

    if (currentTime.isBetween(moment(timeStart, 'HH:mm').subtract(sFlag ? 1 : 0, 'days'), moment(timeEnd, 'HH:mm').add(!sFlag && flag ? 1 : 0, 'days').subtract(sFlag && !flag ? 1 : 0, 'days'))) {
        /* 
        Postoje slede캖i slu캜ajevi:
            Ako nije pro코lo 00:00
                Vreme po캜etka ne diramo
                Ako je vreme zavr코etka pre 00:00
                    Vreme zavr코etka ne diramo
                Ako je vreme zavr코etka posle 00:00
                    Vremenu zavr코etka dodajemo 1 dan
            Ako je pro코lo 00:00
                Vremenu po캜etka oduzimamo 1 dan
                Ako je vreme zavr코etka pre 00:00
                    Vremenu zavr코etka oduzimamo 1 dan
                Ako je vreme zavr코etka posle 00:00 pa do lowerLimita(05:00)
                    Vreme zavr코etka ne diramo

            lowerLimit je vreme pre koga ne sme po캜eti radno vreme a posle koga se ono ne sme zavr코iti
        */
        return true;
    };


    return false;
}

const roundToPrecision = (x, precision) => {
    var y = +x + (precision === undefined ? 0.5 : precision / 2);
    return y - (y % (precision === undefined ? 1 : +precision));
}

export {
    isOpen,
    roundToPrecision
}